.h5ap_playlist {
  display: flex;
  height: auto;
  overflow: hidden;
  float: none;
  .radius {
    overflow: hidden;
  }
  &.alignright {
    float: none;
  }
  &.alignleft {
    float: none;
  }
}

.audioPlaylistCard {
  input[type="range"]::-webkit-slider-runnable-track {
    box-shadow: none;
  }
  #list-screen {
    background-color: #141920;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 9999;
    display: none;
    #list-screen-header {
      background-color: #141920;
      padding: 15px;
      text-align: center;
      height: 25px;
      position: relative;
      color: white;
      font-family: "Lato", sans-serif;
      cursor: pointer;
      box-sizing: content-box;
      img#up-arrow {
        position: absolute;
        top: 23px;
        left: 15px;
        cursor: pointer;
      }
      span.list-active-song-name {
        color: #fff;
        line-height: 25px;
        font-size: 16px;
        font-family: "Lato", sans-serif;
        letter-spacing: 0.5px;
      }
    }
    #list {
      overflow-y: scroll;
      .song {
        padding: 15px;
        background-color: #242b33;
        margin-bottom: 1px;
        cursor: pointer;
        line-height: 24px;
        display: flex;
        span.song-number-now-playing {
          display: inline-block;
          width: 24px;
          text-align: center;
        }
        span.song-number-now-playing span.number {
          opacity: 0.5;
          font-family: "Lato", sans-serif;
          font-size: 14px;
          color: #fff;
          letter-spacing: 0.5px;
        }
        span.song-number-now-playing img.now-playing {
          display: none;
        }
        &.item-active {
          background-color: #30363e;

          .song-number-now-playing img.now-playing {
            display: inline-block;
          }
          .song-number-now-playing span.number {
            display: none;
          }
        }
        div.song-meta-container {
          display: inline-block;
          margin-left: 8px;
          vertical-align: middle;
          width: calc(100% - 85px);
          align-items: center;

          span.song-name {
            font-family: "Lato", sans-serif;
            font-size: 14px;
            line-height: 16px;
            color: #55b560;
            letter-spacing: 0.47px;
            display: block;
          }
          span.song-artist-album {
            font-family: "Lato", sans-serif;
            font-size: 12px;
            line-height: 14px;
            color: #fff;
            letter-spacing: 0.47px;
            display: block;
          }
        }
        span.song-duration {
          display: inline-block;
          opacity: 0.5;
          font-family: "Lato", sans-serif;
          font-size: 14px;
          color: #fff;
          letter-spacing: 0.5px;
          width: 40px;
          text-align: center;
        }
        span.song-artist-album {
          color: #5277b0;
        }
      }
    }

    #list-screen-footer {
      height: 80px;
      padding: 15px;
      #list-screen-meta-container {
        width: calc(100% - 123px);
        float: left;
        .song-name {
          font-family: "Lato", sans-serif;
          font-size: 16px;
          color: #fff;
          letter-spacing: 0.5px;
          line-height: 24px;
        }
        .song-artist-album {
          opacity: 0.5;
          font-family: "Lato", sans-serif;
          font-size: 14px;
          color: #fff;
          letter-spacing: 0.5px;
          line-height: 16px;
        }
      }

      .list-controls {
        width: 90px;
        float: right;
        margin-top: 8px;
        .list-previous {
          cursor: pointer;
          width: 15px;
          height: 17px;
          background-image: url(../../../assets/svg/previous.svg);
          background-repeat: no-repeat;
          float: left;
          margin-right: 15px;
          margin-top: 4px;
          background-size: contain;
        }
        .list-play-pause {
          cursor: pointer;
          width: 17px;
          height: 24px;
          background-repeat: no-repeat;
          float: left;
        }
        .list-play-pause.playing {
          background-image: url(../../../assets/svg/small-pause.svg);
          background-size: contain;
        }
        .list-play-pause.paused {
          background-image: url(../../../assets/svg/small-play.svg);
          background-size: contain;
        }
        .list-next {
          cursor: pointer;
          width: 15px;
          height: 17px;
          background-image: url(../../../assets/svg/next.svg);
          background-repeat: no-repeat;
          float: left;
          margin-left: 15px;
          margin-top: 4px;
          background-size: contain;
        }
      }
    }
  }
  progress {
    cursor: pointer;
  }
  .song-played-progress {
    background: #fff;
    z-index: 99;
    height: 4px;
    position: absolute;
    width: 100%;
    &::-webkit-progress-value {
      background-color: #fff;
    }
  }

  div.player-header {
    background-color: #141920;
    padding: 15px;
    text-align: center;
    height: 25px;
    position: relative;
    margin-top: 40px;
    cursor: pointer;
    color: white;
    font-family: "Lato", sans-serif;
    box-sizing: content-box;
    img#down {
      position: absolute;
      top: 23px;
      left: 15px;
      cursor: pointer;
    }
  }

  #player-top {
    position: relative;
    img[data-amplitude-song-info="cover_art_url"] {
      width: 100%;
      height: 370px;
      object-fit: cover;
    }
  }

  #player-progress-bar-container {
    width: 100%;
    height: 4px;
    background-color: #000;
    position: relative;
    margin-top: -6px;
    progress#song-played-progress {
      width: 100%;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      height: 4px;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: 9;
      border: none;
      cursor: pointer;
      background: transparent;
    }
    progress#song-played-progress[value]::-webkit-progress-bar {
      background: none;
    }
    progress#song-played-progress[value]::-webkit-progress-value {
      background: white;
    }
    progress#song-played-progress[value]::-moz-progress-bar {
      background: white;
    }
    progress#song-buffered-progress {
      width: 100%;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      height: 4px;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: 2;
      border: none;
      background: #6d6d6d;
    }
    progress#song-buffered-progress[value]::-webkit-progress-bar {
      background: none;
    }
    progress#song-buffered-progress[value]::-webkit-progress-value {
      background-color: rgba(255, 255, 255, 0.5);
      transition: width 0.1s ease;
    }
    progress#song-buffered-progress[value]::-moz-progress-bar {
      background: rgba(255, 255, 255, 0.5);
    }
  }

  #player-middle {
    background-color: #000;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    position: relative;
    #time-container {
      color: white;
      width: 100%;
      background-color: #000;
      font-size: 14px;
      font-weight: bold;
    }
    #time-container span.amplitude-duration-time {
      position: absolute;
      top: 0px;
      right: 5px;
    }
    #time-container span.amplitude-current-time {
      position: absolute;
      top: 0px;
      left: 5px;
    }
    .song-name {
      font-family: Lato, sans-serif;
      font-size: 16px;
      color: #fff;
      letter-spacing: 0.5px;
      line-height: 24px;
      display: block;
    }
    .song-artist-album {
      opacity: 0.5;
      font-family: Lato, sans-serif;
      font-size: 14px;
      color: #fff;
      letter-spacing: 0.5px;
      line-height: 16px;
    }
  }

  #player-bottom {
    background-color: #192029;
    padding-left: 25px;
    padding-right: 25px;
    #control-container {
      height: 60px;
      padding-top: 40px;
      padding-bottom: 40px;
      box-sizing: content-box;
      #shuffle-container {
        float: left;
        width: 20%;
        height: 60px;
        padding-top: 21px;
      }
      #shuffle-container #shuffle {
        width: 16px;
        height: 17px;
        cursor: pointer;
        margin: auto;
        opacity: 0.7;
      }
      #shuffle-container #shuffle.amplitude-shuffle-off {
        background: url(../../../assets/svg/shuffle.svg);
      }
      #shuffle-container #shuffle.amplitude-shuffle-off:hover {
        background: url(../../../assets/svg/shuffle-on.svg);
        opacity: 0.8;
      }
      #shuffle-container #shuffle[data-active="true"] {
        background: url(../../../assets/svg/shuffle-on.svg);
        opacity: 1;
      }
      #shuffle-container #shuffle.amplitude-shuffle-on:hover {
        opacity: 1;
      }
      #prev-container {
        width: 20%;
        float: left;
        height: 60px;
        padding-top: 18px;
      }
      #prev-container #previous {
        background: url(../../../assets/svg/previous.svg);
        width: 24px;
        height: 24px;
        cursor: pointer;
        margin: auto;
      }
      #prev-container #previous:hover {
        background: url(../../../assets/svg/previous-hover.svg);
      }
      #play-pause-container {
        width: 20%;
        float: left;
        height: 60px;
        #play-pause {
          width: 60px;
          height: 60px;
          cursor: pointer;
          margin: auto;
          background-image: url(../../../assets/svg/play.svg);
          &.plyr__control--pressed {
            background-image: url(../../../assets/svg/pause.svg);
          }
        }
        #play-pause.amplitude-playing {
          background-image: url(../../../assets/svg/pause.svg);
        }
        #play-pause.amplitude-paused {
          background-image: url(../../../assets/svg/play.svg);
        }
      }

      #next-container {
        width: 20%;
        float: left;
        height: 60px;
        padding-top: 18px;
        #next {
          background-image: url(../../../assets/svg/next.svg);
          width: 24px;
          height: 24px;
          cursor: pointer;
          margin: auto;
        }
        #next:hover {
          background-image: url(../../../assets/svg/next-hover.svg);
        }
      }
      #repeat-container {
        float: left;
        width: 20%;
        height: 60px;
        padding-top: 20px;
        #repeat {
          background-image: url(../../../assets/svg/repeat.svg);
          width: 18px;
          height: 20px;
          cursor: pointer;
          margin: auto;
          opacity: 0.7;
        }
        #repeat:hover {
          background: url(../../../assets/svg/repeat-on.svg);
          opacity: 0.8;
        }
        #repeat[data-active="true"] {
          background: url(../../../assets/svg/repeat-on.svg);
          opacity: 1;
        }
        #repeat.amplitude-repeat-on:hover {
          opacity: 1;
        }
      }
    }

    #control-container::after {
      content: "";
      display: table;
      clear: both;
    }
    #volume-container {
      padding-bottom: 20px;
      text-align: center;
      display: flex;
      align-items: center;
      button {
        background-color: transparent;
        color: #fff;
      }
      img {
        display: block;
        float: left;
        margin-top: -9px;
      }
      input[type="range"].amplitude-volume-slider {
        width: calc(100% - 30px);
        -webkit-appearance: none;
        display: block;
        margin-left: 10px;
        float: left;
      }
      input[type="range"].amplitude-volume-slider:focus {
        outline: none;
      }
      input[type="range"].amplitude-volume-slider::-webkit-slider-runnable-track {
        width: 75%;
        height: 2px;
        cursor: pointer;
        animate: 0.2s;
        background: #cfd8dc;
      }
      input[type="range"].amplitude-volume-slider::-webkit-slider-thumb {
        height: 18px;
        width: 18px;
        background-color: white;
        cursor: pointer;
        margin-top: -8px;
        -webkit-appearance: none;
        border-radius: 20px;
      }
      input[type="range"].amplitude-volume-slider:focus::-webkit-slider-runnable-track {
        background: #cfd8dc;
      }
      input[type="range"].amplitude-volume-slider::-moz-range-track {
        width: 100%;
        height: 1px;
        cursor: pointer;
        animate: 0.2s;
        background: #cfd8dc;
      }
      input[type="range"].amplitude-volume-slider::-moz-range-thumb {
        height: 18px;
        width: 18px;
        background-color: white;
        cursor: pointer;
        margin-top: -8px;
        -webkit-appearance: none;
        border-radius: 20px;
      }
      input[type="range"].amplitude-volume-slider::-ms-track {
        width: 100%;
        height: 2px;
        cursor: pointer;
        animate: 0.2s;
        background: #cfd8dc;
        border-width: 15px 0;
        color: transparent;
        border-color: #192029;
      }
      input[type="range"].amplitude-volume-slider::-ms-fill-lower {
        background: transparent;
        border-radius: 2.6px;
      }
      input[type="range"].amplitude-volume-slider::-ms-fill-upper {
        background: transparent;
        border-radius: 2.6px;
      }
      input[type="range"].amplitude-volume-slider::-ms-thumb {
        height: 18px;
        width: 18px;
        background-color: white;
        cursor: pointer;
        margin-top: 0px;
        border: 20px;
      }
      input[type="range"].amplitude-volume-slider:focus::-ms-fill-lower {
        background: #cfd8dc;
      }
      input[type="range"].amplitude-volume-slider::-ms-fill-upper {
        background: #cfd8dc;
      }
      input[type="range"].amplitude-volume-slider::-ms-tooltip {
        display: none;
      }
    }

    #volume-container:after {
      content: "";
      display: table;
      clear: both;
    }
  }

  @-moz-document url-prefix() {
    #player-bottom #volume-container input[type="range"].amplitude-volume-slider {
      margin-top: 0px;
    }
  }

  @supports (-ms-ime-align: auto) {
    #player-bottom #volume-container input[type="range"].amplitude-volume-slider {
      margin-top: -4px;
      height: 30px;
      background-color: #192029 !important;
    }
  }

  @media all and (-ms-high-contrast: none) {
    #player-bottom #volume-container *::-ms-backdrop,
    .flat-black-player-container #player-bottom #volume-container input[type="range"].amplitude-volume-slider {
      margin-top: -8px;
      background-color: #192029 !important;
      background: #192029 !important;
    }
  }
  /*
        3. Layout
      */
  body {
    background-color: rgba(0, 0, 0, 0.8);
    -webkit-font-smoothing: antialiased;
    line-height: 1.5;
    font-family: "Lato", sans-serif;
  }

  .flat-black-player-container {
    position: relative;
    margin: auto;
    min-width: 350px;
    width: 400px;
    max-width: 100%;
  }

  .slide-in-top {
    -webkit-animation: slide-in-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: slide-in-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  }

  @-webkit-keyframes slide-in-top {
    0% {
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes slide-in-top {
    0% {
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
      opacity: 1;
    }
  }

  .slide-out-top {
    -webkit-animation: slide-out-top 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
    animation: slide-out-top 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  }

  @-webkit-keyframes slide-out-top {
    0% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
      opacity: 0;
    }
  }

  @keyframes slide-out-top {
    0% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
      opacity: 0;
    }
  }
}
