/**
sticky skin
*/
.skin_fusion {
  & .h5ap_standard_player {
    max-width: 100%;
  }
  & .plyr {
    & .plyr__controls {
      background: #1f1f1f !important;
      padding: 0;
      justify-content: space-between;
      & .infos {
        width: 100%;
        max-width: calc(100% - 380px);
        & .time_progress .plyr__progress {
          width: 100%;
          max-width: calc(100% - 100px);
        }
      }
      & .extra_controls {
        margin-bottom: 5px;
      }
      & button[data-active="true"] {
        background: #00b3ff !important;
      }
    }

    & .plyr__control,
    .plyr__control[data-plyr="play"],
    .plyr__time {
      color: #ffffff !important;
    }
    & .title h3 {
      background: #000;
      color: #fff;
      padding: 2px 10px 4px;
      margin: 0;
      font-size: 16px;
      border-radius: 3px;
      text-align: left;
      margin-bottom: 12px;
    }
  }
}
@media screen and (max-width: 768px) {
  .skin_fusion {
    & .plyr .plyr__controls {
      padding: 10px;
      border-radius: 4px;
      & .thumbnails {
        display: none;
      }
      & .infos {
        max-width: 100%;
      }
      & .plyr__controls {
        padding: 0;
      }
    }
  }
}
@media screen and (max-width: 540px) {
  .skin_fusion {
    & .plyr .plyr__controls:first-child {
      flex-direction: column-reverse;
      & .other_controls {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
      }
    }
  }
}

