/**
 sticky player
*/
.h5ap_sticky_player {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0px);
  z-index: 9999;
  max-width: 100% !important;
  &.minimized {
    width: 200px;
    & .thumbnails {
      width: 70px;
      height: 70px;
    }
    & .infos,
    .other_controls {
      display: none;
    }
    & .controller .icon-minus {
      display: none;
    }
    & .controller .icon-plus {
      display: inline-block;
    }
  }
  &.maximized {
    width: 900px;
    & .infos,
    .other_controls {
      display: block;
    }
    & .controller .icon-minus {
      display: inline-block;
    }
    & .controller .icon-plus {
      display: none;
    }
  }
}
