      transition: all 1s;
    }
    & .like {
      position: absolute;
      width: 35px;
      height: 35px;
      top: 15px;
      right: 20px;
      z-index: 15;
      font-size: 1.5em;
      vertical-align: middle;
      line-height: 38px;
      color: #f2f2f2;
      border-radius: 50%;
      text-align: center;
      cursor: pointer;
    }
    & .icon-heart {
      display: block;
    }
    & .mask {
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.2);
      z-index: 2;
      transition: all 1s;
    }
    &-info {
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 4;
      list-style: none;
      color: #262626;
      transform: scale(1);
      margin-top: 16px;
      margin-bottom: 16px;
      text-align: left;
      & li {
        margin-bottom: 7px;
        line-height: 1.15;
      }
      & li:nth-child(1) {
        font-size: 1.5em;
      }
      & li:nth-child(2) {
        font-size: 1.2em;
      }
      & li:nth-child(3) {
        font-size: 0.9em;
      }
    }
    & .info-two {
      left: auto;
      right: 4.5%;
      z-index: 1;
      color: #f2f2f2;
      & li:nth-child(2) {
        float: right;
      }
      & li:nth-child(3) {
        display: inline-block;
        float: right;
        clear: both;
      }
    }
    & #play-button {
      box-sizing: border-box;
      position: absolute;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      background-color: var(--plyr-color-bg);
      box-shadow: 0 8px 25px 6px rgba(0, 0, 0, 0.5);
      right: 40px;
      bottom: 90px;
      z-index: 5;
      cursor: pointer;
      transition: all 70ms;
      border: none;
      &:hover {
        width: 70px;
        height: 70px;
        box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.5);
        right: 45px;
        bottom: 95px;
        transition: all 70ms;
      }
      & .icon-play {
        position: absolute;
        font-size: 1.5em;
        left: 52%;
        top: 50%;
        transform: translate(-48%, -50%);
        transition: all 70ms;
        color: #ffffff;
      }
      &:hover .icon-play {
        font-size: 1.2em;
        transition: all 70ms;
      }
      & .icon-cancel {
        position: absolute;
        font-size: 1.6em;
        left: 50%;
        top: 49%;
        transform: translate(-50%, -51%);
        color: #ffffff;
      }
    }
  }

  .control-row {
    position: absolute;
    bottom: -1px;
    width: 100%;
    height: 130px;
    background-color: #ffffff;
    overflow: hidden;
    z-index: 3;
    .details {
      display: none;
    }
    & #pause-button {
      box-sizing: border-box;
      position: absolute;
      width: 70px;
      height: 70px;
      border-radius: 50%;
      background-color: #f2f2f2;
      left: 40px;
      bottom: 25px;
      z-index: 5;
      cursor: pointer;
      border: none;
      transform: scale(0);
      // display: none;
      animation: scale-animation 0.4s;
      animation-fill-mode: forwards;
      animation-delay: 0.3s;
      & .icon-pause {
        position: absolute;
        font-size: 1.5em;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: all 0.1s;
        color: #424242;
      }
      & .icon-play {
        position: absolute;
        font-size: 1.5em;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: all 0.1s;
        color: #424242;
      }
    }
    & .seek-field {
      bottom: 60px;
      left: 130px;
      position: absolute;
      width: calc(100% - 260px);
      z-index: 5;
      transform: scale(0);
      // display: none;
      animation: scale-animation 0.4s;
      animation-fill-mode: forwards;
      animation-delay: 0.3s;
    }
    & .volume-field {
      bottom: 67px;
      right: 127px;
      position: absolute;
      width: 50px;
      z-index: 5;
      transform: scale(0);
      // display: none;
      animation: scale-animation 0.4s;
      animation-fill-mode: forwards;
      animation-delay: 0.3s;
    }
    & .volume-icon {
      // width: 20px;
      // height: 20px;
      position: absolute;
      border-radius: 50%;
      bottom: 50px;
      // right: 185px;
      z-index: 5;
      font-size: 1.2em;
      // display: none;
      transform: scale(0);
      animation: scale-animation 0.4s;
      animation-fill-mode: forwards;
      animation-delay: 0.3s;
      & .icon-volume-up {
        color: #424242;
      }
    }
  }

  .like-active {
    color: #ef5350;
    animation: scale-animation 0.4s;
    animation-fill-mode: forwards;
  }

  @keyframes scale-animation {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }

  .waves-animation-one {
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: var(--plyr-color-bg);
    z-index: 3;
    left: 42.5%;
    bottom: 20%;
    transform: scale(0);
    animation: waves-animation-one 0.25s;
    animation-fill-mode: forwards;
    animation-delay: 0.3s;
    display: none;
  }

  @keyframes waves-animation-one {
    0% {
      transform: scale(1);
    }
    99% {
      transform: scale(7.8);
    }
    100% {
      transform: scale(7.8);
    }
  }

  .waves-animation-two {
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #5c6bc0;
    z-index: 3;
    right: 40px;
    bottom: 35px;
    transform: scale(7.8);
    display: none;
    animation: waves-animation-two 0.2s;
    animation-fill-mode: forwards;
  }

  @keyframes waves-animation-two {
    to {
      transform: scale(0);
    }
  }

  .info-active {
    animation: info-active-animation 3s;
    animation-fill-mode: forwards;
    animation-delay: 0.37s;
  }

  @keyframes info-active-animation {
    to {
      bottom: 56.5%;
    }
  }

  .play-active {
    animation: play-animation 0.3s;
    animation-fill-mode: forwards;
  }

  @keyframes play-animation {
    0% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    98% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      transform: translate(-200px, 65px);
    }
    99% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      transform: translate(-200px, 65px) scale(0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      transform: translate(0, 65px) scale(1);
    }
  }

  .play-inactive {
    animation: play-inactive 1s;
    animation-fill-mode: forwards;
  }

  @keyframes play-inactive {
    from {
      transform: translate(0, 65px);
    }
  }

  input[type="range"] {
